import React, { useState, useEffect, useContext } from 'react';
import './Carousel.component.scss';
import { motion, AnimatePresence } from 'framer-motion';
import { AppContext } from '../../AppContext';

export default function Carousel() {
  const { isLG, imageIndexObj, images } = useContext(AppContext);
  const [[page, direction], setPage] = useState([0, 0]);
  const [imageIndex, setImageIndex] = imageIndexObj;
  console.log(process.env.REACT_APP_PUBLIC_URL + `${images[imageIndex].link}`);
  const paginate = (newDirection) => {
    setPage([page + newDirection, newDirection]);
    setImageIndex(imageIndex + newDirection);
    if (imageIndex === images.length - 1) {
      setImageIndex(0);
    }
  };

  useEffect(() => {
    const next = document.getElementById('next');
    const interval = setInterval(() => next.click(), 5000);
    return () => {
      clearInterval(interval);
    };
  }, []);

  return (
    <div className="carousel full-vh" id="carousel">
      <div className="carousel-overlay" id="carousel-overlay"></div>
      <AnimatePresence initial={false} custom={direction}>
        <motion.img
          key={page}
          custom={direction}
          className="carousel-image"
          src={process.env.REACT_APP_PUBLIC_URL + `${images[imageIndex].link}`}
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
          transition={{
            opacity: { duration: 1 },
          }}
        />
      </AnimatePresence>
      <div
        className="next"
        onClick={() => paginate(1)}
        id="next"
        transition={{
          opacity: { duration: 1 },
        }}
      >
        <p>+</p>
      </div>
      <div className="prev" onClick={() => paginate(-1)}>
        <p>-</p>
      </div>
      <div className="carousel-text">
        <h1 className={isLG ? `display2` : `display3`}>our work</h1>
      </div>
    </div>
  );
}
